div {
  box-sizing: border-box;
}

p {
  margin: 0;
}

.App {
  text-align: center;
}

ul {
  list-style: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input {
  border-width: 0;
}

input:focus {
  outline: none;
}

.input-container {
  display: flex;
  margin: 20px;
  padding: 20px;
}

.input-container div {
  margin: auto;
}

.credit-input {
  width: 40%;
  margin-top: 5px;
  padding: 10px;
  background-color: whitesmoke;
  border-radius: 5px;
  text-align: center;
}

.credit-input:focus {
  background-color: wheat;
}

.result-container {
  padding-top: 400px;
  display: flex;
  padding: 20px;
  margin: 10px;
}

.result-container div {
  margin: auto;
}

.result-card {
  background-color:ivory;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 1px 1px 1px 1px rgb(219, 219, 219);
}

.calc-btn {
  background-color: bisque;
  border: none;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 40px;
}

.calc-btn:hover {
  cursor: pointer;
}